<!--视频中心-->
<template>
  <div class="video">
    <div class="title">视频中心</div>
    <div class="container">
      <div class="nav-wrap">
        <div class="nav">
          <div class="title">教材选择</div>
          <filter-category @filter-category="handleFilterCategory"></filter-category>
        </div>
      </div>
      <div class="content-wrap">
        <div class="content">
          <div class="search">
            <el-input style="width:320px"
              v-model="resourceName" 
              placeholder="请输入关键字搜索">
               <i @click="handleSearch" slot="suffix" class="el-input__icon el-icon-search"></i>
              </el-input>
            <ul>
              <li :class="{active:resourceType===0}" @click="handleChangeType(0)">全部</li>
              <li :class="{active:resourceType===9060}" @click="handleChangeType(9060)">学科</li>
              <li :class="{active:resourceType===9061}" @click="handleChangeType(9061)">活动</li>
            </ul>
            <div class="search-order">
              <span @click="showOrderSelect=true">{{orderLabel}} <i class="el-icon-arrow-down"></i> </span>
              <ul v-clickOutSide="handleHideOrderSelect" v-if="showOrderSelect">
                <li @click="handleChangeOrder('最新',1)">最新</li>
                <li @click="handleChangeOrder('最热',2)">最热</li>
              </ul>
            </div>
          </div>
          <div class="res-box">
            <div v-for="item in resourceList" :key="item.resourceId" class="box-item">
              <div class="box-item-wrap">
                <div class="bg" @click="handleTo(item.resourceId)">
                  <div>
                    <i class="p-center iconfont icon-video"></i>
                  </div>
                </div>
                <div class="info">
                  <div class="title">{{item.resourceName}}</div>
                  <div>
                    <span>{{item.userName}}</span>
                    <span><i class="iconfont icon-eyes"></i>{{item.visitCount}}</span>
                    <span><i class="icon-stars iconfont"></i>{{item.avgScore}}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="page-wrap">
            <el-pagination
              v-if="resourceList.length>0"
              @current-change="handleCurrentChange"
              :current-page="pagination.currentPage"
              :page-size="pagination.pageSize"
              layout="total, prev, pager, next, jumper"
              :total="pagination.total">
            </el-pagination>
          </div>
        </div>
        
      </div>
    </div>
  </div>
</template>

<script>

export default {
  components:{
    page:() => import("@/components/res-pagination"),
    filterCategory:() => import("@/components/filterCategory")
  },

  data(){
    return {
      showOrderSelect:false,
      orderType: 1, //// 1最新,2最热
      orderLabel:"最新",
      resourceName:'', // 搜索名称
      resourceType: 0, // 导航类型 0.全部 9060.学科 9061.活动
      pagination: {
        total: 100,
        pageSize: 8,
        currentPage: 1
      },
      stageId:"-1",
      subjectId:"-1",
      versionId:"-1",
      gradeId:"-1",
      catalogId:"-1",
      resourceList:[]
    }
  },
  created(){
    this.getInfo()
  },
  methods:{
    debonce(fun,delay){
      let timer = null;
      return (...args) => {
        if(!timer) clearTimeout(timer)
        timer = setTimeout(()=>{
          fun.apply(this,args)
        },delay)
      }
    },
    /* getInfo */
    getInfo(){
      const params = {
        resourceName: this.resourceName,
        resourceFamily: 0,
        resourceFileType:0,
        orderType: this.orderType,
        pageSize: this.pagination.pageSize,
        currPage: this.pagination.currentPage,
        resourceType: this.resourceType,

        stageId: this.stageId,
        subjectId: this.subjectId,
        versionId: this.versionId,
        gradeId: this.gradeId,
        catalogId:this.catalogId
      }
      this.$api.selectResourceData(0, params).then(res => {
        console.log(res);
        this.resourceList = res.data
        this.pagination.total = Number(res.totalDatas)
      });
    },
    /* 隐藏排序下拉 */
    handleHideOrderSelect(){
      this.showOrderSelect = false
    },
    /* 改变排序 */
    handleChangeOrder(label,value){
      this.orderLabel = label;
      this.orderType = value;
      this.handleHideOrderSelect();
      this.pagination.currentPage = 1;
      this.getInfo();
    },
    /* 改变类型 */
    handleChangeType(val){
      this.resourceType = val;
      this.pagination.currentPage = 1;
      this.getInfo();
    },
    /* 页码改变 */
    handleCurrentChange(current){
      this.pagination.currentPage = current;
      this.getInfo();
    },
    handleTo(id){
      this.$router.push(`/resource/Layout/resource-list/${id}`)
    },
    /* 改变目录 */
    handleFilterCategory(val){
      const {stageId,subjectId,versionId,gradeId,catalogId} = val;
      this.stageId = stageId;
      this.subjectId = subjectId;
      this.versionId = versionId;
      this.gradeId = gradeId;
      this.catalogId = catalogId;
      this.pagination.currentPage = 1;
      this.getInfo();
    },
    handleSearch(){
      this.pagination.currentPage = 1;
      this.getInfo();
    }
  }
}
</script>

<style lang="scss" scoped>
.video {padding: 32px;height: 100%}
.video > .title {color: #55595C;font-weight: bold;line-height: 1}
.container {margin-top: 20px;}
.container {
  display: flex;
  height: calc(100% - 34px);
  .nav-wrap {
    flex-basis: 280px;
    margin-right: 30px;
  }
  .nav{
    height: 100%;
    background: #fff;
    border-radius: 6px;
    padding: 23px;
    .title {color: #55595C;line-height: 1;font-weight: bold;margin-bottom: 36px}
  }
  .content-wrap { flex: 1; }
  .content-wrap .content {
    .search {display: flex;align-items: center;padding: 0 10px;justify-content: space-between;}
    .search > ul {display: flex;}
    .search > ul > li {cursor: pointer;text-align: center;margin-left: 8px;width: 52px;height: 28px;line-height: 28px;}
    .search > ul >li.active {color: #fff;background:#4C84FF;border-radius: 14px;}
    .search-order {
      position: relative;
      span {
        cursor: pointer;
      }
      ul {
        width: 100px;
        background: #fff;
        z-index: 99;
        position: absolute;
        right: 0;
        top: 25px;
        border-radius: 4px;
        overflow: hidden;
        li {
          height: 40px;
          padding-left: 10px;
          line-height: 40px;
          cursor: pointer;
          &:hover {
            color: #4C84FF;
            background: #EBEEF5;
          }
        }
      }
    }
    .res-box {
      display: flex;
      flex-wrap: wrap;
      min-height: 632px;
    }
    .res-box .box-item {
      padding: 0 10px;
      width: 25%;
      margin-top: 20px;
    }
    .res-box .box-item .box-item-wrap {
      border-radius: 6px;
      overflow: hidden;
      position: relative;
      top:0;
      transition: all .2s linear;
      &:hover {
        top:-10px;
      }
    }
    .res-box .box-item .bg {
      height: 0;
      padding-bottom: 70%;
      background: url('./../../../assets/image/city-1.jpg') no-repeat;
      background-size: 100% 100%;
      position: relative;
      cursor: pointer;
      &:hover {
        div {display: block;}
      }
      div {
        display: none;
        position: absolute;
        left: 0;
        bottom: 0;
        top: 0;
        right: 0;
        background: rgba(0, 0, 0, 0.2);
        i {font-size: 60px;color: #fff}
      }
    }
    .res-box .box-item .info {
      background: #fff;
      padding: 10px 10px 0;
      background: #fff;
      .title {
        color: #606266;font-weight: bold;
      }
      span {
        color: #909399;
        line-height: 44px;
        margin-right: 20px;
        i {margin-right: 5px;position: relative;top: 1px;}
      }
    }
  }
  .page-wrap {text-align: right;margin-top: 20px;}
  .page-wrap .page {display: inline-block;}
}

</style>
<style>
.video .search .el-input input{
  background: #F1F3F8;
  border-radius: 18px;
  border: 1px solid #DCDFE6;
}
.video .search .el-input__icon {
  width: 35px;
  font-size: 20px;
  line-height: 37px;
}
</style>